/* index.css */
@import url("https://rsms.me/inter/inter.css");
@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

html,
body,
#root {
  background: radial-gradient(circle at 41% 50%, rgb(255, 255, 255), rgb(180, 180, 180));
}

/* Styling for the carousel and canvas container */
.flickity-enabled {
  position: relative;
}

.hDShiW {
  width: 100%;
  max-width: 750px;
}

.jjRdFm {
  max-width: 100%;
  max-height: 100%;
}

.kufCep .flickity-slider > div {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.flickity-slider {
  position: absolute;
  width: 100%;
  height: 100%;
}

.flickity-viewport {
  overflow: hidden;
  position: relative;
  height: 100%;
}

.flickity-prev-next-button.previous {
  left: 10px;
}

.flickity-prev-next-button {
  top: 50%;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  transform: translateY(-50%);
}

.flickity-button {
  position: absolute;
  background: hsla(0, 0%, 100%, 0.75);
  border: none;
  color: #333;
}

.flickity-prev-next-button .flickity-button-icon {
  position: absolute;
  left: 20%;
  top: 20%;
  width: 60%;
  height: 60%;
}

.flickity-button-icon {
  fill: #333;
}

.flickity-prev-next-button.next {
  right: 10px;
}

.configurator-container {
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  max-width: 400px; /* Increased width for a better UI */
  margin-right: 20px;
}

.mesh-label {
  font-size: 16px;
  margin-bottom: 8px;
}

.mesh-navigation {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.nav-button {
  padding: 8px 12px;
  border: none;
  background-color: #f0f0f0;
  border-radius: 4px;
  cursor: pointer;
}

.nav-button[disabled] {
  opacity: 0.5;
  cursor: not-allowed;
}

.tab-buttons {
  display: flex;
  justify-content: space-around;
  margin-bottom: 15px;
}

.tab-button {
  padding: 8px 16px;
  font-size: 16px;
  border: none;
  background-color: transparent;
  color: #555;
  cursor: pointer;
  transition: color 0.3s;
}

.tab-button:hover {
  color: #333;
}

.tab-button.active {
  color: #000;
  font-weight: bold;
  border-bottom: 2px solid #000;
}

.tab-heading {
  font-size: 18px;
  margin-bottom: 10px;
}

/* Styling for the color options */
.color-options {
  display: flex;
  flex-wrap: wrap;
  max-height: 300px; /* 4 rows at 60px height */
  overflow-y: auto;
  padding-right: 10px; /* Add padding for scrollbar */
}
.base-options {
  flex-direction: column;
  max-height: 300px; /* 4 rows at 60px height */
  overflow-y: auto;
  padding-right: 10px; /* Add padding for scrollbar */
}

.color-option {
  width: 72.9px; 
  height: 60px;
  margin-right: 10px;
  flex: 0 0 calc(25% - 10px);
  position: relative;
  margin-bottom: 20px;
  border-radius: 10px;
}

.color-option.selected {
border: 2px solid #333;
}

.exclusive-label {
  margin-top: 10px;
  font-weight: bold;
  display: flex;
  flex-wrap: wrap; 
  grid-column: 1 / span 4;
}

.texture-options,
.exclusive-textures {
  display: flex;
  flex-wrap: wrap; 
  grid-column: 1 / span 4;
}

.textures {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px; 
}

.texture-option {
  width: 72.9px; 
  height: 60px;
  margin-right: 10px;
  margin-bottom: 20px;
  flex: 0 0 calc(25% - 10px);
  position: relative;
  border-radius: 10px;
  grid-column: 1 / span 4;
}

.texture-option.selected {
  border: 2px solid #333;
}

.color-label {
  /* other styles */
  flex-direction: column;
  font-size: 14px;
  text-align: center;
  max-width: 60px; 
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;  
  overflow: hidden;
}

.texture-option-wrapper {
  display: flex;
  flex-direction: column; 
}

.color-option-wrapper {
  width: 82.9px;
  height: 134.39px;
}

.texture-label {
  flex-direction: column; 
  font-size: 14px;
  text-align: center;
  max-width: 60px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;  
  overflow: hidden;
}

/* Add this to your existing CSS or a separate stylesheet */
.radio-container {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  flex-wrap: wrap;
  max-height: 300px; /* 4 rows at 60px height */
  overflow-y: auto;
  padding-right: 10px; /* Add padding for scrollbar */
}

.radio-label {
  margin-left: 8px;
  font-size: 14px;
}

.radio-input {
  position: relative;
  appearance: none;
  width: 16px;
  height: 16px;
  border: 2px solid #000000;
  border-radius: 50%;
  outline: none;
  transition: border-color 0.2s ease-in-out;
  cursor: pointer;
}

.radio-input:checked {
  border-color: #007bff; /* Change this to your desired checked color */
}
